<template>
  <div class="leaveBox">
    <Fixednav></Fixednav>
    <div class="content">
      <div class="tabsBox">
        <ul class="tabs">
          <h1 class="title">快速索引</h1>
          <li class="li-tab" v-for="(item,index) in tabsParam"
              @click="toggleTabs(index)"
              :class="{active:index!=nowIndex}">{{item}}
          </li>
        </ul>
      </div>
      <div class="tab" v-show="nowIndex===0">
        <h3>出国（境）需提供资料清单</h3>
        <div class="info">
          <ul class="info-nav">
            <li class="info-item">清单</li>
            <li class="info-file">参考文件</li>
          </ul>
          <p>1、因公出国经费先行审核意见表</p>
          <p>2、请示</p>
          <p>3、区府批复</p>
          <p class="act">
            <span class="type-left">4、出国（境）审批表、护照（包括签证和出入境记录）复印件</span>
            <span class="file">禅财社【2014】9号 管理办法第十六条。</span>
          </p>
          <p>5、行程安排表</p>
          <p>6、旅行社合同、费用发票及明细清单</p>
          <p>（1）明细清单项目类型</p>
          <p class="type"><span class="type-left">① 国际旅费。指出境口岸至入境口岸旅费，一般为机票、船票、火车票等，据实报销。厅级以下人员乘坐火车硬卧、高铁二等座、软席列车二等软座，轮船三等舱、飞机经济舱(国际旅费凭据报销)</span>
            <span class="file">禅财社【2014】9号 管理办法第八条、第九条。</span></p>
          <p class="type"><span class="type-left">② 交通费。出访国家城市与城市之间的交通费用，事先在出国计划中列明，据实报销。</span>
            <span class="file">禅财社【2014】9号 管理办法第八条、第十条。</span></p>
          <p class="type"><span class="type-left">③ 住宿费。国外发生的住宿费用，厅局级及以下人员安排标准间，在规定的住宿费标准之内予以报销。注意酒店星级</span>
            <span class="file">禅财社【2014】9号 管理办法第八条、第十一条</span></p>
          <p class="type"><span class="type-left">④ 伙食费。国外期间的日常伙食费用，按规定的标准包干使用。</span>
          <span class="file">禅财社【2014】9号 管理办法第八条、第十二条。</span></p>
          <p class="type"><span class="type-left">⑤ 公杂费。在国外期间的市内交通、邮电、办公用品、必要的小费等费用，按规定的标准包干使用。</span>
            <span class="file">禅财社【2014】9号 管理办法第八条、第十二条。</span></p>
          <p class="type"><span class="type-left">⑥ 国外接待费。确需宴请的，应连同出国计划一并报批，宴请标准按照所在国家一人一天的伙食费标准掌握。出访团组与我国驻外使领馆等外交机构和其他中资机构、企业之间一律不得用公款相互宴请。</span>
          <span class="file">禅财社【2014】9号 管理办法第十三条、第十四条。</span></p>
          <p class="type"><span class="type-left">⑦ 其他费用。指出国签证费用、必需的保险费用、防疫费用、国际会议注册费用等</span>
          <span class="file">禅财社【2014】9号 管理办法第八条、第十五条。</span></p>
          <p class="other">对于在国外举办的投资推介活动，建议在其他费用列支，并列出明细项目。</p>
          <p>对于通过旅行社出国（境）的，旅行社的服务费是列支在其他费用还是单独开具发票报账？</p>
        </div>
      </div>
      <div class="tab" v-show="nowIndex===1">
        <form action="">
          <input type="text" name="cityName" placeholder="请输入目的地（如：首尔；韩国，其他城市）" class="findBox">
          <input type="submit" value="查询" class="btn" @click="getData">
        </form>
      </div>
    </div>
  </div>
</template>
<script>
  import Fixednav from '@/components/nav/fixed_nav'

  export default {
    name: 'leave',
    data () {
      return {
        tabsParam: ['资料清单', '出国（境）开支标准'],
        nowIndex: 0
      }
    },
    methods: {
      toggleTabs: function (index) {
        this.nowIndex = index
      },
      getData: function () {
        this.$http.post('api/abroad/query/', { body: { cityName: '朝鲜' } }).then(response => {
          console.log(response.date)
        }, response => {
          console.log('error')
        })
      }
    },
    components: {
      Fixednav
    }
  }
</script>
<style scoped>
  .leaveBox {
    overflow: hidden;
  }

  .content{
   overflow: hidden;
    margin-top: 155px;
    max-width: 84%;
    margin-left: auto;
    margin-right: auto;
  }
  .tabs {
    width: 15%;
    border-right: 1px solid grey;
    float: left;
  }
  .tab{
    width: 70%;
    float: left;
    padding-left: 140px;
  }

  .title {
    font-size: larger;
    font-weight: 600;
    margin-bottom: 1rem;
  }

  .li-tab{
    padding: 20px 0;
    cursor: pointer;
  }
  .li-tab:hover{
    color: blue;
  }
  .tab h3{
    width: 100%;
    font-size: 33px;
    font-weight: 600;
    display: block;
    text-align: center;
    border-bottom: 1px solid #333;
    padding-bottom: 20px;
  }
  .info{
    margin-top: 50px;
    border-top: 1px solid #333;
  }
  .info-nav{
    overflow: hidden;
    margin-bottom: 20px;
  }
  .info-nav li{
    float: left;
    font-size: 16px;
    margin-top: 5px;
  }
  .info p{
    font-size: 14px;
    line-height: 38px;
  }
  .info-item{
    width: 50%;
    text-align: center;
  }
  .info-file{
    width: 50%;
    text-align: center;
  }
  .file{
    display: inline-block;
    margin-left: 60px;
  }
  .act{
    border-bottom: 1px solid #333;
  }
  .type{
    border-bottom: 1px solid #333;
  }
  .type-left{
    display: inline-block;
    width: 50%;
  }
  .other{
    margin-top: 30px;
  }
  .findBox{
    width: 250px;
    height: 30px;
  }
  .btn{
    width: 50px;
    height: 32px;
  }
  @media screen and (max-width: 976px){
    .content{
      margin-top: 250px;
      max-width: 976px;
      height: 1900px;
      margin-left: auto;
      margin-right: auto;
    }
    .tabs {
      width: 100%;
      border-right: 1px solid grey;
    }
    .tab{
      width: 100%;
      padding-left: 0;
    }
    .title {
      font-size: larger;
      font-weight: 600;
      margin-bottom: 1rem;
      text-align: center;
    }

    .li-tab{
      text-align: center;
      cursor: pointer;
    }
    .tab h3{
      width: 100%;
      font-size: 20px;
      font-weight: 600;
      display: block;
      text-align: center;
      border-bottom: 1px solid #333;
      padding-bottom: 20px;
    }
    .info{
      margin-top: 50px;
      border-top: 1px solid #333;
    }
    .info-nav{
      overflow: hidden;
      margin-bottom: 20px;
    }
    .info-nav li{
      float: left;
      font-size: 14px;
      margin-top: 5px;
    }
    .info p{
      font-size: 12px;
      line-height: 38px;
    }
    .info-item{
      width: 50%;
      text-align: center;
    }
    .info-file{
      width: 50%;
      text-align: center;
    }
    .file{
      width: 40%;
      display: inline-block;
      float: right;
      margin-left: 0;
      color: red;
    }
    .act{
      border-bottom: 1px solid #333;
      overflow: hidden;
      height: 80px;
    }
    .type{
      border-bottom: 1px solid #333;
      overflow: hidden;
    }
    .type-left{
      display: inline-block;
      float: left;
      width: 60%;
    }
    .other{
     padding-top: 20px;
    }
  }
</style>



