<template>
  <div class="receptionBox">
    <Fixednav></Fixednav>
    <div class="content">
      <div class="tabsBox">
      <ul class="tabs">
        <h1 class="title">快速索引</h1>
        <li class="li-tab" v-for="(item,index) in tabsParam"
            @click="toggleTabs(index)"
            :class="{active:index!=nowIndex}">{{item}}
        </li>
      </ul>
      </div>
      <div class="tab" v-show="nowIndex===0">
        <h3>国内公务接待需提供资料清单</h3>
        <ul class="fileBox">
          <li class="file" v-for="(filename,index) in aboutFile">{{filename}}</li>
        </ul>
      </div>
      <div class="tab" v-show="nowIndex===1">我是外宾</div>
      <div class="tab" v-show="nowIndex===2">我是招商</div>
    </div>
  </div>
</template>
<script>
  import Fixednav from '@/components/nav/fixed_nav'

  export default {
    data () {
      return {
        tabsParam: ['国内公务', '外宾', '招商引资'],
        aboutFile: ['粤办发〔2014〕12号'],
        nowIndex: 0
      }
    },
    methods: {
      toggleTabs: function (index) {
        this.nowIndex = index
      }
    },
    components: {
      Fixednav
    }
  }
</script>
<style scoped>
  .receptionBox {
    overflow: hidden;
  }

  .content{
    overflow: hidden;
    margin-top: 155px;
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
  }
  .tabs {
    width: 15%;
    border-right: 1px solid grey;
    float: left;
  }
  .tab{
    width: 60%;
    float: left;
    padding-left: 170px;
  }

  .title {
    font-size: larger;
    font-weight: 600;
    margin-bottom: 1rem;
  }

  .li-tab{
    padding: 1rem 0;
    cursor: pointer;
  }
  .file{
    width: 25%;
    display: inline-block;
    color: red;
    font-size: 16px;
    text-align: center;
    padding: 15px 0;
    margin-bottom: 25px;
  }
  .tab h3{
    width: 100%;
    font-size: 36px;
    font-weight: 600;
    display: block;
    text-align: center;
    border-bottom: 1px solid #333;
    padding-bottom: 20px;
  }
  .info p{
    font-size: 20px;
    line-height: 38px;
  }
</style>



